:root {
    & h1,
    & h2,
    & h3,
    & h4,
    & h5,
    & h6 {
        margin: 0;
    }

    & a {
        color: inherit;
        text-decoration: none;
    }

    & p {
        margin: 0;
    }

    & ul {
        padding: 0;
        margin: 0;
    }

    & ul,
    & li {
        list-style: none;
    }
}
